<template>
  <div class="col-md-12">
    <div class="card card-container border-light">
      <img id="profile-img" class="mx-auto profile-img-card" style="max-height: 20rem; max-width: 18rem" src="//ssl.gstatic.com/accounts/ui/avatar_2x.png"/>
      <Form @submit="handleLogin" :validation-schema="schema">
        <div class="form-group">
          <label for="username" class="fs-4 fw-bold">Identifiant</label>
          <Field name="username" type="text" class="form-control" />
          <ErrorMessage name="username" class="error-feedback" style="color:red" />
        </div>
        <div class="form-group">
          <label for="password" class="fs-4 fw-bold">Mot de passe</label>
          <Field name="password" type="password" class="form-control" />
          <ErrorMessage name="password" class="error-feedback" style="color:red" />
        </div>

        <div class="form-group">
          <button class="btn btn-primary btn-block connection mt-2" :disabled="loading">
            <span
              v-show="loading"
              class="spinner-border spinner-border-sm"
            ></span>
            <span class="">Connection</span>
          </button>
        </div>

        <div class="form-group">
          <div v-if="message" class="alert alert-danger" role="alert">
            {{ message }}
          </div>
        </div>
      </Form>
    </div>
  </div>
  <Footer />
</template>

<script>
import { Form, Field, ErrorMessage } from "vee-validate";
import * as yup from "yup";
import Footer from "../components/Footer";

export default {
  name: "Login",
  components: {
    Footer,
    Form,
    Field,
    ErrorMessage,
  },
  data() {
    const schema = yup.object().shape({
      username: yup.string().required("L'identifiant utilisateur est requis !"),
      password: yup.string().required("Le mot de passe est requis !"),
    });

    return {
      loading: false,
      message: "",
      schema,
    };
  },
  computed: {
    loggedIn() {
      return this.$store.state.auth.status.loggedIn;
    },
  },
  created() {
    if (this.loggedIn) {
      this.$router.push("/Groupomania-app/forum");
    }
  },
  methods: {
    handleLogin(user) {
      this.loading = true;

      this.$store.dispatch("auth/login", user).then(
        () => {
          this.$router.push("/Groupomania-app/forum");
        },
        (error) => {
          this.loading = false;
          this.message =
            (error.response &&
              error.response.data &&
              error.response.data.message) ||
            error.message ||
            error.toString();
        }
      );
    },
  },
};
</script>

